import React from 'react';
import CloseIcon from '../assets/img/social/close.png';
import SuccessImage from '../assets/img/success_icon.svg';
import { TextButton } from '../components/Button';
import MediaQuery from 'react-responsive';

export function ModalPhone(props) {
	return (
		<div className='modal' style={{display: props.visible? 'flex': 'none'}}>
            <MediaQuery minWidth={601}>
                <div className='modal__wrapper'>
                    <img src={CloseIcon} onClick={props.close} alt='Close' style={{ color: '#244084', width: 25, height: 25, position: 'absolute', top: 30, right: 30, cursor: 'pointer'}}/>
                    <span className='modal__header dark__blue' style={{marginTop: 40}}>
                        เบอร์โทรศัพท์ของคุณ
                    </span>
                    <div style={{display: 'flex', flexDirection: 'column'}}>
                        <input 
                            className={`modal__input ${props.inputError? 'error': ''}`} 
                            placeholder='0801234567' 
                            type="text" 
                            pattern="\d*" 
                            maxLength="10" 
                            onChange={props.onPhoneInput}
                            value={props.phoneValue}
                        />
                        <span className={`error__text ${props.inputError? 'show': ''}`}>*กรุณากรอกเบอร์โทร 9-10 หลัก</span>
                    </div>
                    <button className='modal__button' style={{marginTop: 6}} onClick={props.submit} disabled={props.inputError}>ยืนยัน</button>
                </div>
                <div className='modal__footer'>
                    {props.footer}
                </div>
            </MediaQuery>

            <MediaQuery maxWidth={600}>
                <div className='modal__wrapper'>
                    <img src={CloseIcon} onClick={props.close} alt='Close' style={{ color: '#244084', width: 25, height: 25, position: 'absolute', top: 30, right: 30, cursor: 'pointer'}}/>
                    <MediaQuery minWidth={361}>
                        <span className='modal__header dark__blue' style={{marginTop: 90}}>
                            เบอร์โทรศัพท์ของคุณ
                        </span>
                    </MediaQuery>
                    <MediaQuery maxWidth={360}>
                        <span className='modal__header dark__blue' style={{marginTop: 65}}>
                            เบอร์โทรศัพท์ของคุณ
                        </span>
                    </MediaQuery>
                    <div style={{display: 'flex', flexDirection: 'column'}}>
                        <input 
                            className={`modal__input ${props.inputError? 'error': ''}`} 
                            placeholder='0801234567' 
                            type="text" 
                            pattern="\d*" 
                            maxLength="10" 
                            onChange={props.onPhoneInput}
                            value={props.phoneValue}
                        />
                        <span className={`error__text ${props.inputError? 'show': ''}`}>*กรุณากรอกเบอร์โทร 9-10 หลัก</span>
                    </div>

                    <div className='modal__footer' style={{marginTop: 20}}>
                        {props.footer}
                    </div>
                    <button className='modal__button' style={{marginTop: 15}} onClick={props.submit} disabled={props.inputError}>ยืนยัน</button>

                    <MediaQuery minWidth={361}>
                        <span style={{fontSize: 18, display: 'flex', marginTop: 25}}>
                            <span className='dark__blue'>โทรหาเราได้เสมอที่</span>
                            <a href="tel:+6620791081" className='blue' style={{textDecoration: 'underline'}}> 02-079-1801</a>
                        </span>
                    </MediaQuery>
                    <MediaQuery maxWidth={360}>
                        <span style={{fontSize: 16, display: 'flex', marginTop: 25}}>
                            <span className='dark__blue'>โทรหาเราได้เสมอที่</span>
                            <a href="tel:+6620791081" className='blue' style={{textDecoration: 'underline'}}> 02-079-1801</a>
                        </span>
                    </MediaQuery>
                </div>
            </MediaQuery>
		</div>
	)
}

export function ModalSuccess(props) {
	return (
        <div className='modal' style={{display: props.visible? 'flex': 'none'}}>
			<div className='modal__wrapper modal__success'>
                <img src={CloseIcon} onClick={props.close} alt='Close' style={{ color: '#244084', width: 25, height: 25, position: 'absolute', top: 30, right: 30, cursor: 'pointer'}}/>
                <MediaQuery minWidth={601}>
                    <img src={SuccessImage} alt='About us' style={{width: 25, height: 25, marginBottom: 6, marginTop: 42}}/>
                </MediaQuery>

                <MediaQuery maxWidth={600} minWidth={361}>
                    <img src={SuccessImage} alt='About us' style={{width: 25, height: 25, marginBottom: 6, marginTop: 95}}/>
                </MediaQuery>

                <MediaQuery maxWidth={360}>
                    <img src={SuccessImage} alt='About us' style={{width: 25, height: 25, marginBottom: 6, marginTop: 60}}/>
                </MediaQuery>
                <span className='modal__header blue' style={{marginTop: 8}}>
                    ได้รับเบอร์เรียบร้อยแล้ว
                </span>

                <span className='modal__body dark__blue' style={{marginTop: 8}}>
                    เราจะโทรหาคุณภายใน 24 ชั่วโมง<br></br> ที่เบอร์ <font className='blue'>{props.phoneNumber}</font>
                </span>

                <button className='modal__button' style={{marginTop: 41}} onClick={props.close}>ปิด</button>
                <TextButton 
					slot='แก้ไขเบอร์โทร'
					style={{marginTop: 20, textDecoration: 'underline'}}
					onClick={props.back}
				/>	
            </div>

            <MediaQuery minWidth={601}>
            <div className='modal__footer'>
                {props.footer}
            </div>
            </MediaQuery>
		</div>
    )
}
